import './polyfills';
import { cssObserve } from './css-observer';
import { safeWindowOpen } from '@castify/safe-window-open';

let _port: chrome.runtime.Port;

function listenForDomEvents() {
  cssObserve('.VfPpkd-xl07Ob-XxIAqe-OWXEXe-FNFY6c').subscribe((elem) =>
    onClassroomMenuFound(elem),
  );
}

function onClassroomMenuFound(rootEl: Element) {
  const listElement = rootEl.firstChild;
  const item = document.createElement('li');
  // These classes are all specific to Google Classroom CSS as of 2022-01-26
  item.classList.add('VfPpkd-StrnGf-rymPhb-ibnC6b');
  item.innerHTML = `<span class="VfPpkd-StrnGf-rymPhb-f7MjDc"><svg viewBox="0 0 38 30"><g fill="none" fill-rule="evenodd"><path fill="#FFF" d="M11 8h12v13H11z"/><path fill="#FFF" d="M22 12l6-4v12l-6-3z"/><path d="M2 8L0 9v11l2 1h9V8H2zM36 13l-8-5-15-8-2 1v7h9l2 1v3l5-4v12l-5-3v3l-2 1h-9v6l2 2 15-9 8-4v-3z" fill="#FF8282" fill-rule="nonzero"/></g></svg>
  </span>
  <span class="VfPpkd-StrnGf-rymPhb-b9t22c">Video assignment</span>`;

  // insert before the separator line in the menu pop-up
  const separatorElement = document.querySelectorAll('[role="separator"]')[0];
  listElement.insertBefore(item, separatorElement);

  postPendoMessage('submit-button-shown');

  item.addEventListener('click', () => {
    postPendoMessage('submit-button-clicked');
    safeWindowOpen('https://app.screencastify.com/submit');
  });
}

function postPendoMessage(action) {
  _port.postMessage({
    type: 'pendo',
    data: { action },
  });
}

async function main() {
  _port = chrome.runtime.connect({ name: 'submitButtonInClassroom' });

  listenForDomEvents();
}

main();
