import { Observable, animationFrameScheduler, concat, defer, from } from 'rxjs';
import { filter, tap, observeOn } from 'rxjs/operators';

/**
 * @fileoverview
 * Tooling for observing elements on a 3rdparty page.
 * Can be used for example to add a record button to Google Docs, a Submit button
 * to Google Classroom etc.
 */

/**
 * Do not re-emit items previously emitted by source observable.
 */
function weakFilterDupes() {
  return (obs: Observable<Element>) => {
    // Only keep a weak reference to allow GC to cleanup items.
    const matches = new WeakSet();
    return obs.pipe(
      filter((item) => !matches.has(item)),
      tap((item) => matches.add(item)),
    );
  };
}

/**
 * Observe document for items matching given css selector.
 * Initially emit all existing elements on the page.
 * Emit matching items before next content pane.
 *
 * Does NOT work in some cases yet
 * (e.g. changing of parent node attributes that should trigger childs to match).
 * We could use querySelectorAll() after a batched set of DOM modifications?
 *
 *
 * Example usage:
 * ```ts
 * cssObserve('.foo-interesting-element').subscribe(el => el.appendChild(...))
 * ```
 * Playground: https://codesandbox.io/s/css-observe-yby4e?file=/src/index.ts
 */
export function cssObserve(selector: string): Observable<Element> {
  const obs = new Observable<Element>((obs) => {
    const mutationObserver = new MutationObserver((items) => {
      // Query all elements if any attributes changed.
      let queryAll = false;
      for (const item of items) {
        if (item.type === 'childList') {
          for (let i = 0; i < item.addedNodes.length; ++i) {
            const node: Element = item.addedNodes[i] as Element;
            if (node.nodeType !== 1) continue;
            if (node.matches(selector)) {
              obs.next(node);
            }
          }
        }
        if (item.type === 'attributes') {
          const node = item.target as Element;
          if (node.nodeType !== 1) {
            continue;
          }
          queryAll = true;
          break;
        }
      }
      if (queryAll) {
        const matches = document.querySelectorAll(selector);
        for (let i = 0; i < matches.length; i++) {
          obs.next(matches[i]);
        }
      }
    });
    mutationObserver.observe(document.documentElement, {
      subtree: true,
      attributes: true,
      childList: true,
    });
    return () => mutationObserver.disconnect();
  });
  const existing$ = defer(() => from(document.querySelectorAll(selector)));

  return concat(
    existing$,
    obs.pipe(observeOn(animationFrameScheduler), weakFilterDupes()),
  );
}
